<template>
  <div class="secondary-chart-container">
    <div class="analitic__secondary-chart">
      <canvas ref="secondaryChart" class="secondary-chart__data"></canvas>
    </div>
  </div>
</template>

<script>
import {dataStore} from "@/operations/requests";
import {Chart, registerables} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';

Chart.register(...registerables, ChartDataLabels);

export default {
  name: "SecondaryChartWithAvailableComponent",
  data() {
    return {
      dataForAnalysis: [],
      goodsWithAvailable: 0,
      goodsOutOfhAvailable: 0,
    }
  },
  methods: {
    createDataForChart() {
      this.dataForAnalysis = [...dataStore.getGoodAndReservData()];
      this.getWithOutCount();
      this.showCharts();
    },

    getWithOutCount() {
      const dataWithAvailable = this.dataForAnalysis.filter(item => (item.Available + item.AvailableDpm + item.Available2320 + item.Reserv) !== 0);
      const dataOutOfhAvailable = this.dataForAnalysis.filter(item => (item.Available + item.AvailableDpm + item.Available2320 + item.Reserv) === 0);

      this.goodsWithAvailable = dataWithAvailable;
      this.goodsOutOfhAvailable = dataOutOfhAvailable;
    },
    showCharts() {
      this.destroyChart();
      this.tradeMarkChart();
    },

    destroyChart() {
      if (this.chart) {
        this.chart.destroy();
        this.chart = null;
      }
    },
    tradeMarkChart() {
      const data = {
        labels: [`С остатком`, `Без остатка`],
        datasets: [{
          label: 'Едениц',
          data: [this.goodsWithAvailable.length, this.goodsOutOfhAvailable.length],
          backgroundColor: [
            'rgba(153, 102, 255, 0.8)',
            'rgba(54, 162, 235, 0.8)',
          ],
          borderColor: 'white',
          hoverOffset: 3
        }]
      };

      const options = {
        responsive: true,
        plugins: {
          legend: {
            position: 'top', // Расположение легенды
          },
          tooltip: {
            enabled: true, // Включение тултипов
          },
          subtitle: {
            display: true,
            text: 'Соотношение товара с остатком и без остатка',
            font: {
              weight: 'bold'
            }
          },
          datalabels: {
            color: '#ffffff', // Цвет текста
            anchor: 'center', // Расположение метки
            align: 'center', // Выравнивание метки
            formatter: (value) => value !== 0 ? value.toLocaleString() : ''
          }
        }
      };

      // Инициализация Doughnut Chart
      const ctx = this.$refs.secondaryChart.getContext('2d');
      this.chart = new Chart(ctx, {
        type: 'doughnut', // Тип графика: doughnut
        data: data,
        options: options
      });
    },
  },
  mounted() {
    this.createDataForChart();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.update();
      }
    });
  },
  beforeUnmount() {
    document.removeEventListener('resize', this.handleEscape);
  }
}
</script>

<style scoped>
.secondary-chart-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

  .analitic__secondary-chart {

    canvas {
      height: 350px !important;
      max-width: 350px !important;
      margin: 10px;
    }
  }
}
</style>