<template>
  <div class="main-chart-container">
    <div class="analitic__main-chart">
      <div class="main-chart">
        <canvas ref="mainChart" class="main-chart__data"></canvas>
      </div>
      <div class="main-chart__buttons">
        <div class="count-data__btn">
          <v-btn class="count-btn"
                 color="teal"
                 size="small"
                 @click="showTrade"
          >
            <span>Торговые марки</span></v-btn>
          <v-btn class="count-btn"
                 color="teal"
                 size="small"
                 @click="showGrope"
          >
            <span>Категории</span></v-btn>
          <v-btn class="count-btn"
                 color="deep-purple-lighten-2"
                 size="small"
                 @click="showOnStore"
          >
            <span>С остатком</span></v-btn>
          <v-btn class="count-btn"
                 color="blue-lighten-1"
                 size="small"
                 @click="showOutOfStore"
          >
            <span>Без остатка</span></v-btn>
        </div>
        <div class="summ-data__btn">
          <v-btn class="summ-btn"
                 color="teal"
                 size="small"
                 @click="showAllGoods"
          >
            <span>Товар</span></v-btn>
          <v-btn class="summ-btn"
                 color="green-darken-1"
                 size="small"
                 @click="showAvailable"
          >
            <span>Доступно</span></v-btn>
          <v-btn class="summ-btn"
                 color="orange-darken-1"
                 size="small"
                 @click="showReserve"
          >
            <span>Резерв</span></v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {dataStore} from "@/operations/requests";
import {Chart, registerables} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';

Chart.register(...registerables, ChartDataLabels);

export default {
  name: "MainChartComponent",
  data() {
    return {
      dataForAnalysis: [],

      tmData: [],
      tmCountData: [],
      tmCount: 0,

      gropeData: [],
      gropeCountData: [],
      gropeCount: 0,

      onStoreData: [],
      onStoreCountData: [],
      onStoreCount: 0,

      outOfStoreData: [],
      outOfStoreCountData: [],
      outOfStoreCount: 0,

      availableAllData: [],
      availableAllCountData: [],
      availableAllCount: 0,

      availableData: [],
      availableCountData: [],
      availableCount: 0,

      reservData: [],
      reservCountData: [],
      reservCount: 0,
    }
  },
  methods: {
    createDataForChart() {
      this.dataForAnalysis = [...dataStore.getGoodAndReservData()];

      this.getTradeMarkData();
      this.getGropeData();
      this.getOnStoreData();
      this.getOutOfStoreData();
      this.getAllGoodsData();
      this.getAvailableData();
      this.getReserveData();

      this.showTrade();
    },

    getTradeMarkData() {
      const counts = this.dataForAnalysis.reduce((acc, item) => {
        acc[item.Tm] = (acc[item.Tm] || 0) + 1;
        return acc;
      }, {});

      const finalArr = Object.entries(counts)
          .map(([tm, count]) => ({tm, count}))
          .sort((a, b) => b.count - a.count);

      const tmArray = finalArr.map(item => item.tm);
      const tmCountArray = finalArr.map(item => item.count);

      this.tmData = tmArray;
      this.tmCountData = tmCountArray;
      this.tmCount = tmArray.length;
    },
    getGropeData() {
      const counts = this.dataForAnalysis.reduce((acc, item) => {
        acc[item.Grope] = (acc[item.Grope] || 0) + 1;
        return acc;
      }, {});

      const finalArr = Object.entries(counts)
          .map(([grope, count]) => ({grope, count}))
          .sort((a, b) => b.count - a.count);

      const gropeArray = finalArr.map(item => item.grope);
      const gropeCountArray = finalArr.map(item => item.count);
      const gropeTotalCount = finalArr.reduce((sum, item) => sum + item.count, 0);

      this.gropeData = gropeArray;
      this.gropeCountData = gropeCountArray;
      this.gropeCount = gropeTotalCount;
    },
    getOnStoreData() {
      const goods = this.dataForAnalysis.filter(item => item.Available !== 0);

      const groupedData = goods.reduce((acc, item) => {
        if (!acc[item.Grope]) {
          acc[item.Grope] = 0;
        }
        acc[item.Grope] += 1;
        return acc;
      }, {});

      const resultArray = Object.entries(groupedData)
          .map(([grope, available]) => ({grope, available,}))
          .sort((a, b) => b.available - a.available);

      const onStoreArray = resultArray.map(item => item.grope);
      const onStoreCountArray = resultArray.map(item => item.available);
      const onStoreTotalCount = resultArray.reduce((sum, item) => sum + item.available, 0);

      this.onStoreData = onStoreArray;
      this.onStoreCountData = onStoreCountArray;
      this.onStoreCount = onStoreTotalCount;
    },
    getOutOfStoreData() {
      const goods = this.dataForAnalysis.filter(item => item.Available === 0);

      const groupedData = goods.reduce((acc, item) => {
        if (!acc[item.Grope]) {
          acc[item.Grope] = 0;
        }
        acc[item.Grope] += 1;
        return acc;
      }, {});

      const resultArray = Object.entries(groupedData)
          .map(([grope, notAvailable]) => ({grope, notAvailable,}))
          .sort((a, b) => b.notAvailable - a.notAvailable);

      const outOfStoreArray = resultArray.map(item => item.grope);
      const outOfStoreCountArray = resultArray.map(item => item.notAvailable);
      const outOfStoreTotalCount = resultArray.reduce((sum, item) => sum + item.notAvailable, 0);

      this.outOfStoreData = outOfStoreArray;
      this.outOfStoreCountData = outOfStoreCountArray;
      this.outOfStoreCount = outOfStoreTotalCount;
    },
    getAllGoodsData() {
      const groupedData = this.dataForAnalysis.reduce((acc, item) => {
        if (!acc[item.Grope]) {
          acc[item.Grope] = 0;
        }
        acc[item.Grope] += (item.Reserv + item.Available + item.Available2320 + item.AvailableDpm);
        return acc;
      }, {});

      const resultArray = Object.entries(groupedData)
          .map(([grope, available]) => ({grope, available}))
          .sort((a, b) => b.available - a.available);

      const availableAllArray = resultArray.map(item => item.grope);
      const availableAllCountArray = resultArray.map(item => item.available);
      const availableAllTotalCount = resultArray.reduce((sum, item) => sum + item.available, 0);

      this.availableAllData = availableAllArray;
      this.availableAllCountData = availableAllCountArray;
      this.availableAllCount = availableAllTotalCount;
    },
    getAvailableData() {
      const groupedData = this.dataForAnalysis.reduce((acc, item) => {
        if (!acc[item.Grope]) {
          acc[item.Grope] = 0;
        }
        acc[item.Grope] += (item.Available + item.AvailableDpm + item.Available2320);
        return acc;
      }, {});

      const resultArray = Object.entries(groupedData)
          .map(([grope, available]) => ({grope, available}))
          .sort((a, b) => b.available - a.available);

      const availableArray = resultArray.map(item => item.grope);
      const availableCountArray = resultArray.map(item => item.available);
      const availableTotalCount = resultArray.reduce((sum, item) => sum + item.available, 0);

      this.availableData = availableArray;
      this.availableCountData = availableCountArray;
      this.availableCount = availableTotalCount;
    },
    getReserveData() {
      const groupedData = this.dataForAnalysis.reduce((acc, item) => {
        if (!acc[item.Grope]) {
          acc[item.Grope] = 0;
        }
        acc[item.Grope] += item.Reserv;
        return acc;
      }, {});

      const resultArray = Object.entries(groupedData)
          .map(([grope, reserv]) => ({grope, reserv}))
          .sort((a, b) => b.reserv - a.reserv);

      const reservArray = resultArray.map(item => item.grope);
      const reservCountArray = resultArray.map(item => item.reserv);
      const reservTotalCount = resultArray.reduce((sum, item) => sum + item.reserv, 0);

      this.reservData = reservArray;
      this.reservCountData = reservCountArray;
      this.reservCount = reservTotalCount;
    },

    destroyChart() {
      if (this.chart) {
        this.chart.destroy();
        this.chart = null;
      }
    },

    showTrade() {
      this.destroyChart();
      let message = `Товар в разрезе торговых марок (${this.tmCount.toLocaleString()} позиций)`
      this.tradeMarkChart(this.tmData, this.tmCountData, message, '#00897B', '#4DB6AC', '#00897B');
    },
    showGrope() {
      this.destroyChart();
      let message = `Товар в разрезе категорий (${this.gropeCount.toLocaleString()} позиций)`
      this.tradeMarkChart(this.gropeData, this.gropeCountData, message, '#00897B', '#4DB6AC', '#00897B');
    },
    showOnStore() {
      this.destroyChart();
      let message = `Товар с остатком в разрезе категорий (${this.onStoreCount.toLocaleString()} позиций)`
      this.tradeMarkChart(this.onStoreData, this.onStoreCountData, message, '#7E57C2', '#B39DDB', '#7E57C2');
    },
    showOutOfStore() {
      this.destroyChart();
      let message = `Товар без остатка в разрезе категорий (${this.outOfStoreCount.toLocaleString()} позиций)`
      this.tradeMarkChart(this.outOfStoreData, this.outOfStoreCountData, message, '#1E88E5', '#64B5F6', '#1E88E5');
    },
    showAllGoods() {
      this.destroyChart();
      let message = `Доступный и зарезервированный товар в разрезе категорий (${this.availableAllCount.toLocaleString()} едениц)`
      this.tradeMarkChart(this.availableAllData, this.availableAllCountData, message, '#00897B', '#4DB6AC', '#00897B');
    },
    showAvailable() {
      this.destroyChart();
      let message = `Доступные остатки товара в разрезе категорий (${this.availableCount.toLocaleString()} едениц)`
      this.tradeMarkChart(this.availableData, this.availableCountData, message, '#388E3C', '#66BB6A', '#388E3C');
    },
    showReserve() {
      this.destroyChart();
      let message = `Зарезервированные остатки товара в разрезе категорий (${this.reservCount.toLocaleString()} едениц)`
      this.tradeMarkChart(this.reservData, this.reservCountData, message, '#F57C00', '#FFA726', '#F57C00');
    },

    tradeMarkChart(data, count, message, colorUp, colorDown, colorText) {
      let title = `Едениц`;
      const ctx = this.$refs.mainChart.getContext('2d');
      const gradient = ctx.createLinearGradient(0, 0, 0, 400); // Вертикальный градиент
      gradient.addColorStop(0, colorUp); // Цвет в начале
      gradient.addColorStop(1, colorDown); // Цвет в конце

      this.chart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: data,
          datasets: [{
            label: title,
            data: count,
            backgroundColor: gradient,
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: false,
            subtitle: {
              display: true,
              text: message,
              font: {
                weight: 'bold',
                size: '15'
              },
              padding: {
                top: 5,
                bottom: 35 // Отступ снизу текста
              }
            },
            datalabels: {
              color: colorText, // Цвет текста
              anchor: 'end', // Расположение метки
              align: 'top', // Выравнивание метки
              formatter: (value) => value !== 0 ? value.toLocaleString() : ''
            }
          },
          scales: {
            y: {
              beginAtZero: true,
            },
            x: {
              grid: {
                display: false, // Отключение сетки по оси X
              },
            }
          }
        },
      });
    },
  },
  mounted() {
    this.createDataForChart();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.update();
      }
    });
  },
  beforeUnmount() {
    document.removeEventListener('resize', this.handleEscape);
  }
}
</script>

<style scoped lang="scss">
.main-chart-container {
  margin-bottom: 20px;

  .analitic__main-chart {
    width: 95%;
    margin: 0 auto;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    border-radius: 3px;
    background-image: linear-gradient(to top, #dee6ff, seashell);
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .main-chart {
      width: 100%;
      height: 400px;
      max-height: 400px;
      min-height: 300px;

      .main-chart__data {
        max-height: 400px;
      }
    }

    .main-chart__buttons {
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      margin: 10px 0 5px 0;

      .count-data__btn {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 10px;
      }

      .summ-data__btn {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 10px;
      }
    }
  }
}
</style>