<template>
  <v-empty-state
      v-if="!isShowChartData"
      headline="Whoops, 404"
      image="https://vuetifyjs.b-cdn.net/docs/images/logos/v.png"
  ></v-empty-state>

  <div class="analitic-container" v-if="isShowChartData">
    <MainChartComponent/>
    <div class="secondaty-chart">
      <SecondaryChartNewComponent/>
      <SecondaryChartWithAvailableComponent/>
      <SecondaryChartFullAvailableComponent/>
    </div>

  </div>
</template>

<script>
import MainChartComponent from "@/components/chart/MainChartComponent.vue";
import SecondaryChartNewComponent from "@/components/chart/SecondaryChartNewComponent.vue";
import SecondaryChartWithAvailableComponent from "@/components/chart/SecondaryChartWithAvailableComponent.vue";
import SecondaryChartFullAvailableComponent from "@/components/chart/SecondaryChartFullAvailableComponent.vue";

export default {
  name: "AnaliticComponent",
  components: {
    SecondaryChartFullAvailableComponent,
    SecondaryChartWithAvailableComponent, SecondaryChartNewComponent, MainChartComponent
  },
  data() {
    return {
      currentRole: 'promoter',
      screenWidth: window.innerWidth,
      isShowChartData: '',
    }
  }, methods: {
    checkRole() {
      let role = JSON.parse(localStorage.getItem("sale-auth"));
      this.currentRole = role.role;
    },
    isShowChart() {
      if (this.currentRole === 'promoter' || this.screenWidth < 990) {
        this.isShowChartData = false;
      } else {
        this.isShowChartData = true;
      }
    },
    updateWidth() {
      this.screenWidth = window.innerWidth;
    },
  },
  watch: {
    screenWidth(newVal) {
      if (newVal < 990) {
        this.isShowChartData = false;
      } else if (newVal > 989) {
        this.isShowChartData = true;
      }
    },
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
  },
  mounted() {
    this.checkRole();
    this.isShowChart();
    localStorage.setItem('checkLoadStatus', 'loaded');
  },
  beforeUnmount() {
    localStorage.removeItem('checkLoadStatus');
    window.removeEventListener('resize', this.updateWidth);
  }
}
</script>

<style lang="scss" scoped>
.analitic-container {
  .secondaty-chart {
    width: 95%;
    margin: 0 auto;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    border-radius: 3px;
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    gap: 10px;
    background-image: linear-gradient(to top, #dee6ff, seashell);
  }
}
</style>