<template>
  <div class="secondary-chart-container">
    <div class="analitic__secondary-chart">
      <canvas ref="secondaryChart" class="secondary-chart__data"></canvas>
    </div>
  </div>
</template>

<script>
import {dataStore} from "@/operations/requests";
import {Chart, registerables} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';

Chart.register(...registerables, ChartDataLabels);

export default {
  name: "SecondaryChartNewComponent",
  data() {
    return {
      dataForAnalysis: [],
      goodsNewCount: 0,
      goodsOldCount: 0,
    }
  },
  methods: {
    createDataForChart() {
      this.dataForAnalysis = [...dataStore.getGoodAndReservData()];
      this.getNewOldCount();
      this.showCharts();
    },

    getNewOldCount() {
      const dataNew = this.dataForAnalysis.filter(item => item.Status == 'Новинка');
      const dataOld = this.dataForAnalysis.filter(item => item.Status == 'Постоянный ассортимент');

      this.goodsNewCount = dataNew;
      this.goodsOldCount = dataOld;
    },
    showCharts() {
      this.destroyChart();
      this.tradeMarkChart();
    },

    destroyChart() {
      if (this.chart) {
        this.chart.destroy();
        this.chart = null;
      }
    },
    tradeMarkChart() {
      const data = {
        labels: [`Новый товар`, `Старый товар`],
        datasets: [{
          label: 'Едениц',
          data: [this.goodsNewCount.length, this.goodsOldCount.length],
          backgroundColor: [
            'rgba(255,0,0,0.9)',
            'rgba(128, 128, 128,0.9)',
          ],
          borderColor: 'white',
          hoverOffset: 3
        }]
      };

      const options = {
        responsive: true,
        plugins: {
          legend: {
            position: 'top', // Расположение легенды
          },
          tooltip: {
            enabled: true, // Включение тултипов
          },
          subtitle: {
            display: true,
            text: 'Соотношение нового и старого товара',
            font: {
              weight: 'bold'
            }
          },
          datalabels: {
            color: '#ffffff', // Цвет текста
            anchor: 'center', // Расположение метки
            align: 'center', // Выравнивание метки
            formatter: (value) => value !== 0 ? value.toLocaleString() : ''
          }
        }
      };

      // Инициализация Doughnut Chart
      const ctx = this.$refs.secondaryChart.getContext('2d');
      this.chart = new Chart(ctx, {
        type: 'doughnut', // Тип графика: doughnut
        data: data,
        options: options
      });

    },
  },
  mounted() {
    this.createDataForChart();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.update();
      }
    });
  },
  beforeUnmount() {
    document.removeEventListener('resize', this.handleEscape);
  }
}
</script>

<style scoped>
.secondary-chart-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

  .analitic__secondary-chart {

    canvas {
      height: 350px !important;
      max-width: 350px !important;
      margin: 10px;
    }
  }
}
</style>